#about {
  background: #111111;
  overflow: hidden;
}

/* AboutDescription */
#about-description {
  overflow: hidden;
  padding: 40px 0;
}

#about-description svg {
  display: block;
  margin: 0 auto 40px auto;
  width: 300px;
}

#about-description-text p {
  display: block;
  text-align: justify;
  text-align-last: center;
  text-justify: auto;
  margin: 20px auto;
  width: 40%;
  min-width: 300px;
  color: #ededed;
  text-transform: uppercase;
  font-family: 'Rajdhani';
  font-size: 14px;
}

/* ClientLinks */
#client-links {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  background: red;
}

#client-links a {
  -ms-flex-preferred-size: 50%;
      flex-basis: 50%;
  -ms-flex-positive: 1;
      flex-grow: 1;
  min-width: 300px;
  height: 50vw;
}

#client-links button {
  position: relative;
  width: 100%;
  height: 100%;
  border: none;
  background-position: center;
  background-size: cover;
  cursor: pointer;
  -webkit-filter: saturate(100%);
          filter: saturate(100%);
  -webkit-transition: -webkit-filter .7s;
  transition: -webkit-filter .7s;
  -o-transition: filter .7s;
  transition: filter .7s;
  transition: filter .7s, -webkit-filter .7s;
}

#client-links button:hover {
  -webkit-filter: saturate(0%);
          filter: saturate(0%);
}



