#private-events {
  background: #111111;
}

#private-events-info h2 {
  font-family: 'Oswald';
  font-size: 24px;
  text-transform: uppercase;
  width: 60%;
  margin: 40px auto;
  text-align: center;
}

#private-events-info p {
  font-size: 16px;
  margin: 40px auto;
  line-height: 2em;
  width: 60%;
  text-align: center;
  display: block;
}

#private-events-info ul {
  display: block;
  width: 60%;
  margin: 40px auto;
  text-align: center;
}

#private-events-info li {
  font-family: arial;
  font-size: 16px;
  margin-left: 15px;
  margin-bottom: 20px;
  color: #ededed;
  line-height: 2em;
  list-style: none;
}

.private-events-image {
  background-position: center;
  background-size: cover;
  height: 50vw;
  opacity: .75;
}

/* FormInstructions */
#form-instructions {
  width: 60%;
  margin: 80px auto;
}

#form-instructions h2 {
  font-family: 'Oswald';
  font-size: 24px;
  text-transform: uppercase;
}

#form-instructions a {
  color: #e75e26;
  -webkit-text-decoration-color: #e75e26;
          text-decoration-color: #e75e26;
}

/* FinalNote */
#final-note {
  width: 60%;
  margin: 80px auto;
}

#final-note h2 {
  font-family: 'Oswald';
  font-size: 24px;
  text-transform: uppercase;
}

#final-note a {
  color: #e75e26;
  -webkit-text-decoration-color: #e75e26;
          text-decoration-color: #e75e26;
}



